.page-main {
  line-height: 21px;
  text-transform: uppercase;
  color: #000000;
}

.page-main__wrapper-head {
  text-align: center;
  padding: 0 4px;
}

.page-main__title {
  width: 284px;
  font-size: 16px;
  font-weight: 700;
  line-height: 25px;
  margin: auto;
  margin-top: 41px;
  margin-bottom: 32px;
}

.page-main__subtitle {
  width: 300px;
  margin: auto;
  margin-bottom: 25px;
  font-size: 14px;
  font-weight: 400;
  line-height: 24px;
}

@media (min-width: 768px) {
  .page-main__wrapper-head {
    text-align: center;
  }

  .page-main__title {
    width: 490px;
    margin: auto;
    margin-top: 49px;
    font-size: 18px;
    line-height: 26px;
  }

  .page-main__subtitle {
    width: auto;
    margin: auto;
    margin-top: 30px;
    margin-bottom: 52px;
  }
}

@media (min-width: 1200px) {
  .page-main__title {
    margin-top: 50px;
    font-size: 18px;
    line-height: 26px;
  }

  .page-main__subtitle {
    margin-top: 28px;
    margin-bottom: 49px;
  }
}
