.sedona {
  position: relative;
  min-width: 320px;
  height: 389px;
  background-color: #deecf5;
}

.sedona__text-index,
.sedona__text,
.sedona__text--form {
  display: none;
}

.sedona__img {
  width: 100%;
  height: 100%;
}

.sedona__wrepper {
  width: 100%;
  position: absolute;
  top: 93.5%;
  left: 0.1%;
}

.sedona__triangle {
  width: 100%;
}

.sedona--photo,
.sedona--form {
  position: relative;
  height: 157px;
}

.sedona__wrepper--photo {
  width: 100%;
  top: 83.999%;

  & .sedona__triangle {
    width: 100%;
  }
}

@media (min-width: 768px) {
  .sedona {
    position: relative;
    min-width: 766px;
    height: 519px;
    margin-top: 9px;
    background-color: #6e93be;
  }

  .sedona__text-index {
    position: absolute;
    display: block;
    top: 15%;
    right: 20%;
  }

  .sedona__text {
    position: absolute;
    display: block;
    top: 30%;
    right: 20%;
  }

  .sedona__text--form {
    top: 32%;
    right: 20%;
  }

  .sedona__img {
    width: 100%;
    height: 100%;
  }

  .sedona__wrepper {
    width: 100%;
    top: 89.2%;
    left: 0%;
  }

  .sedona__triangle {
    width: 100%;
    height: 57px;
  }

  .sedona--photo,
  .sedona--form {
    min-width: 768px;
    position: relative;
    height: 286px;
    z-index: 0;
  }

  .sedona__wrepper--photo {
    width: 100%;
    top: 80.9%;
    left: 0.0001%;

    & .sedona__triangle {
      width: 100%;
    }
  }
}

@media (min-width: 1200px) {
  .sedona {
    width: 100%;
    height: 516px;
    background-color: #6e93be;
  }

  .sedona__text-index {
    top: 15%;
    right: 31%;
  }

  .sedona__text {
    top: 33%;
    right: 31%;
  }

  .sedona__triangle {
    width: 1200px;
    height: 57px;
  }

  .sedona--photo,
  .sedona--form {
    width: 1200px;
    position: relative;
    height: 300px;
  }

  .sedona__wrepper--photo {
    width: 100%;
    top: 81.8%;
    left: 0%;

    & .sedona__triangle {
      width: 100%;
    }
  }
}
